<template>
  <div v-if="!getUserInfo" class="loginBox" >
  <v-dialog max-width="500">
  <template v-slot:activator="{ props: activatorProps }">
    <v-btn
      v-bind="activatorProps"
      color="surface-variant"
      text="회원가입"
      variant="flat"
    ></v-btn>
  </template>

  <template v-slot:default="{ isActive }">
    <v-card title="회원가입">
    <form
    @submit.prevent="[handleRegister(), isActive.value = false]"
    >
      <v-card-text>
        <div class="register-form">
          <div class="input-group">
            <label>이메일(한화)</label>
            <input v-model="email" type="email" required />
          </div>
          <div class="input-group">
            <label>&nbsp;&nbsp;비 밀&nbsp;&nbsp;번 호 </label>
            <input v-model="password" type="password" required />
          </div>
          <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
      </div>
      </v-card-text>
    
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          text="가입하기"
          type="submit"
        ></v-btn>
        <v-btn
          text="Close"
          @click="isActive.value = false"
        ></v-btn>
      </v-card-actions>
      </form>
    </v-card>
  </template>
</v-dialog>
   <v-dialog max-width="500">
  <template v-slot:activator="{ props: activatorProps }">
    <v-btn
      v-bind="activatorProps"
      color="surface-variant"
      text="Login"
      variant="flat"
    ></v-btn>
  </template>

  <template v-slot:default="{ isActive }">
    <form @submit.prevent="login">
    <v-card title="로그인하기">
      <v-card-text>
        <p><input type="text" placeholder="Email" v-model="email" /></p>
        <p><input type="password" placeholder="Password" v-model="password" /></p>
        <p v-if="errorMessage">{{ errorMessage }}</p>
      </v-card-text>
    
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          text="로그인"
          type="submit"
          @click="[login(), isActive.value = false]"
        ></v-btn>

        <v-btn
          text="Close"
          @click="isActive.value = false"
        ></v-btn>
      </v-card-actions>
    </v-card>
    </form>
  </template>
</v-dialog>   

  </div>
</template>

<script>
import { ref } from 'vue';
import { getAuth, signInWithEmailAndPassword, createUserWithEmailAndPassword  } from 'firebase/auth';
import { mapActions,mapGetters } from 'vuex'


const errorMessage = ref('');
import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: "AIzaSyCi7Amm-GJwXg_nGwUE9lcRuRNSck6IUs0",
  authDomain: "hrrule-6b0c7.firebaseapp.com",
  databaseURL: "https://hrrule-6b0c7-default-rtdb.asia-southeast1.firebasedatabase.app",
  projectId: "hrrule-6b0c7",
  storageBucket: "hrrule-6b0c7.firebasestorage.app",
  messagingSenderId: "204591349031",
  appId: "1:204591349031:web:d21503b790597a861a3d02",
  measurementId: "G-G402Y4RD91"
};

export default {
  computed:{
    ...mapGetters(['getUserInfo']),
  },
  data() {
    return {
      email: '',
      password: '',
      errorMessage: ''
    }
  },
  mounted(){
    initializeApp(firebaseConfig);
  },
  methods:{
    ...mapActions(['updateUser']),
    async login(){
      const auth = getAuth();
      try {
        const userCredential = await signInWithEmailAndPassword(auth, this.email, this.password);
        const user = userCredential.user.email
        alert('Successfully logged in:', user);
        this.$store.dispatch('updateUser', user); 
        this.setCookie("savedUser", user)
        this.$router.push('/Team-status'); // 가입 후 리다이렉션
      } catch (error) {
        
        switch (error.code) {
          
          case 'auth/invalid-email':
            errorMessage.value = 'Invalid email format.';
            alert(errorMessage.value)
            break;
          case 'auth/user-not-found':
            errorMessage.value = 'No account found with this email.';
            alert(errorMessage.value)
            break;
          case 'auth/wrong-password':
            errorMessage.value = 'Incorrect password.';
            alert(errorMessage.value)
            break;
          default:
            errorMessage.value = 'Login failed. Please try again.';
            alert(errorMessage.value)
        }
    }
  },
async handleRegister(){
  
  if (!this.email || !this.password) {
    errorMessage.value = '모든 필드를 입력해주세요.';
    alert("모든 필드를 입력해주세요.")
    return;
  }

  try {
    const userCredential = await createUserWithEmailAndPassword( getAuth(),
      this.email, this.password
    );
    alert("가입에 성공하였습니다.")
    this.$router.push('/'); // 가입 후 리다이렉션
  } catch (error) {
    switch (error.code) {
      case 'auth/email-already-in-use':
        errorMessage.value = '이미 사용 중인 이메일입니다.';
        alert(errorMessage.value)
        break;
      case 'auth/invalid-email':
        errorMessage.value = '유효하지 않은 이메일 형식입니다.';
        alert(errorMessage.value)
        break;
      case 'auth/weak-password':
        errorMessage.value = '비밀번호는 6자 이상이어야 합니다.';
        alert(errorMessage.value)
        break;
      default:
        errorMessage.value = '가입 실패: ' + error.message;
    }
  }
},
  setCookie(name, value, expireDays = 1, path = '/'){
    
    document.cookie = `${name}=${encodeURIComponent(value)}`; 

  }
}
}



</script>


<style>
.loginBox{
  width: 300px;
  display: flex;
  justify-content: space-evenly;
}

input {
  border: 0.1px solid #A4CBF9 !important; /* 원하는 색상으로 변경 */
}

</style>
