<template>
  <div class="contentContainer" ref="contentBody">
    <div class="content-body" >
      <section
      v-for="item in items"
      :key="item.id"
      :id="item.id"
      :data-section="item.id"
      ref="sections">
        <div v-html="item.content"></div>
        </section>
    </div> 
  </div>
</template>

<script>
const regulations = require('../assets/motherRule.json');
import { mapGetters,mapState,mapActions } from 'vuex'
import auth from '../assets/auth.json';

export default {
  data() {
    const processedRules = regulations.flatMap(item => item.rules);
    return {
      items: processedRules
    }
  },
  computed: {
    ...mapState(['sections', 'currentSection']),
    ...mapGetters(['getTargetOrg','getUserInfo']),
  },
  methods: {
    ...mapActions(['updateCurrentSection', 'updateScrollPosition']),
      checkLogIn(){ // 로그인안되면 홈으로 돌아가기 

        function isIncludesIn(target, arr) { // 배열 b를 생성하여 객체의 "mail" 값을 저장
          const allMyUsers = arr.map(obj => obj["mail"]);
          
          const result = allMyUsers.includes(target);
          return result;
        }
        const isMyUser = isIncludesIn(this.getUserInfo, auth)
          if(!isMyUser){
            alert("권한이 없습니다. 관리자에게 문의하세요")
             window.location.href = "/new.io/";
          } else{
            
            return  
          }
      },
  },
  mounted() {
    alert("준비중입니다.")
    this.checkLogIn()
    this.$refs.contentBody.addEventListener('scroll', this.handleScroll, true);
    this.$store.dispatch('initializeSections', regulations);
  },
  beforeUnmount() {
    this.$refs.contentBody.removeEventListener('scroll', this.handleScroll, true);
  }
}




</script>

<style>
    .centerAlign{
      text-align: center;
    }
    .rightAlign{
      text-align: right;
    }
    .twoSpaces {
      display: inline-block;
      width: 20px;
    }
    .contentContainer {
      margin: 0 auto;
      padding: 20px;
      display: grid;
      justify-content: space-evenly;
      overflow-y: scroll;
    }
    .content-body {
      display: grid;
      justify-content: space-evenly;
      width: 860px;
      box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
      background-color: white;
      line-height: 30px;
    }

    body {
      font-family: ;
      color: black;
      text-align:left;
    }
    table{
      text-align:center;
      margin-left: 60px;
    }
    .title {
      text-align: center;
      font-size: 21px;
      font-family: "바탕체", serif;
      color: black;
      text-decoration: underline;
      font-weight: bold;
    }
    .date {
      text-align: right;
      font-size: 19px;
      font-family: "바탕체", serif;
      color: black;
    }
    .divider {
      text-align: center;
      font-size: 19px;
      font-family: "바탕체", serif;
      color: black;
    }
    table, th, td {
        border: 1px solid black;
        border-collapse: collapse;
        font-size: 17px;
        line-height: 25px;
    }
    .fourSpaces {
      display: inline-block;
      width: 40px;
    }
    .fiveSpaces {
      display: inline-block;
      width: 50px;
    }
    .sixSpaces {
      display: inline-block;
      width: 60px;
    }
    .sevenSpaces {
      display: inline-block;
      width: 70px;
    }
    .eightSpaces {
      display: inline-block;
      width: 80px;
    }
    ul,li{
      line-height:30px;
      margin-left:30px;
      margin-top:0
    }
</style>