import { createStore } from 'vuex';

const store = createStore({
  state: {
    currentSection: '',
    sections: [],
    scrollPosition: 0,
    targetOrg: "",
    connectedUser: "",
    isMyUser:false,
  },
  mutations: {
    SET_CURRENT_SECTION(state, sectionId) {
      state.currentSection = sectionId
    },
    SET_SECTIONS(state, sections) {
      state.sections = sections
    },
    SET_SCROLL_POSITION(state, position) {
      state.scrollPosition = position
    },
    SET_TargetOrg(state,teamName){
      state.targetOrg = teamName
    },
    SET_ConnectedUser(state, user){
      state.connectedUser = user
    }
  },
  actions: {
    updateTargetOrg({ commit }, teamName) {
      commit('SET_TargetOrg', teamName)
    },
    updateCurrentSection({ commit }, sectionId) {
      commit('SET_CURRENT_SECTION', sectionId)
    },
    updateScrollPosition({ commit }, position) {
      commit('SET_SCROLL_POSITION', position)
    },
    initializeSections({ commit }, sections) {
      commit('SET_SECTIONS', sections)
    },
    updateUser({ commit }, user) {
      
      commit('SET_ConnectedUser', user)
    },
  },
  getters: {
    getMessage(state) {
      return state.message; // 상태 데이터 접근자
    },
    getAllSections(state) {
      return state.sections
    },
    getCurrentSection: state => state.currentSection,
    getTargetOrg: state => state.targetOrg,
    getUserInfo: state =>state.connectedUser,
  },
});

export default store;