<template>
  <v-card>
    <v-layout>
  <v-card class="mx-auto sidebar" width="26vh" height="90vh" style="height: 90vh; overflow: auto">
  <v-list>
    <v-list-group
      v-for="regulation in regulations"
      :key="regulation.id"
      v-model="regulation.isOpen"
    >
      <template v-slot:activator="{ props }">
        <v-list-item v-bind="props">
          <v-list-item-content>

            <v-list-item-title>{{ regulation.category }}</v-list-item-title>

          </v-list-item-content>
        </v-list-item>
      </template>

      
    <v-list-item
      v-for="rule in regulation.rules"
      :key="rule.id"
      :title="rule.구분"
      :value="rule.id"
      :prepend-icon="rule.icon"
      :class="{ 'highlight': isCurrentSection(rule.id) }"
      @click="showTargetItem(rule.구분)"
    >
      </v-list-item>
      
    </v-list-group>
  </v-list>

  </v-card>
  
      <v-main style="height: 90vh; overflow: auto" ref="contentBody" @scroll="handleScroll($event)">
      <router-view/>
      </v-main>
    </v-layout>
  </v-card>
</template>


<script>
import { mapGetters, mapActions,mapState } from 'vuex'


export default {
  computed: {
    ...mapGetters(['getAllSections', 'getCurrentSection']),
    ...mapState(['sections', 'currentSection']),
    regulations() {
      return this.getAllSections;
    },
    currentSection() {
      return this.getCurrentSection;
    }
  },
  methods: {
    ...mapActions(['updateCurrentSection','updateScrollPosition','updateTargetOrg']),
    isCurrentSection(id) {
      return this.getCurrentSection === id;
    },
    showTargetItem(teamName){
      this.updateTargetOrg(teamName)
    },
    handleScroll() {
      
      // 1. 스크롤 컨테이너 요소 가져오기
      const scrollContainer = this.$refs.contentBody.$el
      
      
      // 2. 모든 섹션 요소 선택 (router-view 내부 요소)
      const sections = document.querySelectorAll('[data-section]') // 데이터 속성으로 식별 권장
      // 또는 클래스로 선택: const sections = document.querySelectorAll('.content-section')

      // 3. 현재 가장 많이 보이는 섹션 찾기
      let maxVisibility = 0
      let currentSectionId = null
      const containerHeight = scrollContainer.clientHeight
      const scrollTop = scrollContainer.scrollTop

      sections.forEach(section => {
        // 섹션의 상대적 위치 계산
        const sectionTop = section.offsetTop - scrollContainer.offsetTop
        const sectionHeight = section.offsetHeight

        // 가시 영역 계산
        const visibility = Math.min(
          scrollTop + containerHeight - sectionTop,
          sectionHeight,
          sectionTop + sectionHeight - scrollTop
        )

        // 최대 가시성 섹션 업데이트
        if (visibility > maxVisibility) {
          maxVisibility = visibility
          currentSectionId = section.id
        }
      })

      // 4. 현재 섹션 ID 처리 (콘솔 출력 예시)
      if (currentSectionId) {
        this.updateCurrentSection(currentSectionId); // 본문의 해당 섹션으로 스크롤
      }
    }
  }
}
</script>


<style>

.v-list-group__items .v-list-item {
  padding-left: 18px !important;
  
}

.v-list-group__items .v-list-group__items .v-list-item {
  padding-left: 18px !important;
}
.v-list-item-title{
  font-size: 1rem !important;
  height: 1.5rem !important;
}

.v-list-item {
  min-height: 30px !important; /* 원하는 높이로 조절 */
}

.highlight {
  background-color: rgba(0, 0, 0, 0.1);
  font-weight: bold;
}

.sidebar {
  /* 폭 고정 (원하면 25vh로 바꿔도 됨) */
  flex: 0 0 26vh;        /* grow:0, shrink:0, basis:26vh */
  max-width: 26vh;
  min-width: 26vh;
}

</style>