 /* eslint-disable */

 import { createWebHistory, createRouter } from 'vue-router';
 import HomeView from '../src/components/HomeView.vue'
 import HrRules from '../src/views/HrRules.vue'
 import TeamStatus from '../src/views/TeamStatus.vue'
 import InformationsView from '../src/views/InformationsView.vue'
  
 const routes = [
     {
       path: '/',
       name: 'HomeView',
       component: HomeView
     },
     {
        path: '/hrRules',
        name: 'HrRules',
        component: HrRules
      },
      {
        path: '/informations',
        name: 'InformationsView',
        component: InformationsView
      },
      {
        path: '/team-status',
        name: 'TeamStatus',
        component: TeamStatus
      },
      {
        path: "/login",
        name: "Login",
        component: () => import("../src/views/LogIn.vue"),
      },
      { path: '/new-employee-manual' ,
        name: '매뉴얼',
        component: () => import("../src/views/NewEmployeeManual.vue"),
      },
      {
        path: "/team-role-matrix",
        name: "TeamRole",
        component: () => import("../src/views/TeamRole.vue"),
      },
      { path: '/company-intro' ,
        name: 'CompanyIntro',
        component: () => import("../src/views/CompanyIntro.vue"),
      },
      { path: '/seating-chart' ,
        name: '좌석배치도',
        component: () => import("../src/views/SeatingChart.vue"),
      }
   ]
 
 const router = createRouter({
   history: createWebHistory(),
   routes,
 });
 
 export default router;
 