<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-img
          :src="require('../assets/한화로고.png')"
          class="my-3"
          contain
          height="200"
        />
      </v-col>

      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Welcome to hr.HPS Beta
        </h1>


        <p class="subheading font-weight-regular">
          에러/요청사항은 인사지원팀에 문의하시기 바랍니다.,
        </p>
      </v-col>

    </v-row>
     <v-sheet
    class="d-flex align-center justify-center flex-wrap text-center mx-auto px-4"
    elevation="4"
    height="250"
    max-width="800"
    width="100%"
    rounded
  >
    <div style="display:flex; align-items: center;flex-direction: column ">
      <h2 v-if="logIned"
      class="text-h4 font-weight-black text-orange">환영합니다!</h2>
      <h2 v-if="notLoggedIn"
      class="text-h4 font-weight-black text-orange">
      환영합니다! {{ userName }} 님</h2>

      <div class="text-h5 font-weight-medium mb-2">
        본 페이지는 한화파워시스템 테스트 페이지입니다. 
      </div>

      <p class="text-body-2 mb-4" v-if="logIned">
        데이터에 접근하기 위한 로그인을 하시기 바랍니다.
      </p>
      
      <LogIn v-if="logIned"/>
      <LogIn v-if="!logIned"/>
      <v-btn
          text="LogOut"
          @click="userLogout"
          v-if="!logIned"
      />

    </div>
  </v-sheet>

  </v-container>
</template>

<script>

import LogIn from '../views/LogIn.vue'
import { mapGetters,mapActions } from 'vuex'

export default {
  name: 'HomeView',
  methods:{
    ...mapActions(['updateUser']),
    userLogout(){ 
      this.$store.dispatch('updateUser', null); 
      this.setCookie("savedUser", "")
    },
    setCookie(name, value){
    document.cookie = `${name}=${encodeURIComponent(value)}`; 
  }
  },
  data: () => ({}),
  computed: {
    ...mapGetters(['getUserInfo']),
    logIned() {
      return !this.getUserInfo
    },
    notLoggedIn(){
      return this.getUserInfo
    },
    userName(){
      if(this.getUserInfo){
        const userName = this.getUserInfo.replace(/[0-9]/g, "")
        return userName
      }
      return ""
    }
  },
  components: {
    LogIn
  },
}
</script>
